}

.button {
  font-size: 18px;
  width: fit-content;
  padding: 14px 50px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  transition: color 0.4s cubic-bezier(0.215, 0.61, 0.355, 1);
  background: transparent;
}

.button .main-text {
  transition: transform 0.4s cubic-bezier(0.215, 0.61, 0.355, 1);
  display: inline-block;
}

.button__bg {
  width: 100%;
  height: 100%;
  background: #292B33;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  transform: translateY(100%);
  transition: transform 0.4s cubic-bezier(0.215, 0.61, 0.355, 1);
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.button__bg span {
  transform: translateY(100%);
  display: inline-block;
  transition: transform 0.4s cubic-bezier(0.215, 0.61, 0.355, 1);
  transition-delay: .2s;
}

.button:hover {
  color: white;
}

.button:hover span {
  transform: translateY(-100%);
}

.button:hover .button__bg {
  transform: translateY(0%);
}

.button:hover .button__bg span {
  transform: translateY(0%);
}

.button__white {
  border: 1px white solid;
  color: white;
}

.button__white .button__bg {
  background: white;
}

.button__white:hover {
  color: #292B33;
}

.button__slider {
  margin-top: 80px;
  margin-left: auto;
  margin-right: auto;
  opacity: 0;
}

.nav {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 1100;
  display: none;
}

.nav__items {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
  margin-left: 10vw;
}

.nav__item {
  font-size: 40px;
  font-family: 'Ogg-Roman', 'Cormorant Garamond', serif;
  font-weight: 400;
  margin-top: 5vh;
}

.nav__item:nth-child(1) {
  margin-top: 0px;
}

.nav__item a {
  text-decoration: none;
  transition: opacity .4s ease;
  color: #292B33;
  opacity: 0.7;
}

.nav__item a:hover {
  opacity: 1;
}

.nav__item a:hover span {
