}

.nav__rewealer {
  width: 100%;
  height: 100%;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}

.nav__wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav__wrapper svg {
  fill: white;
}

.nav__contacts {
  display: flex;
  flex-direction: column;
  margin-right: 10vw;
  font-family: 'Avenir Regular', sans-serif;
  font-weight: 400;
  font-size: 21px;
  margin-top: 70px;
