/*  BURGER
========================================== */
.burger {
  width: 50px;
  height: 50px;
  position: relative;
  border-radius: 4px;
  margin-left: 10px;
}

.burger span {
  position: relative;
  margin-top: 9px;
  margin-bottom: 9px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -15px;
  margin-top: -1.5px;
}

.burger span, .burger span::before, .burger span::after {
  display: block;
  width: 30px;
  height: 3px;
  background-color: black;
  outline: 1px solid transparent;
  -webkit-transition-property: background-color, -webkit-transform;
  -moz-transition-property: background-color, -moz-transform;
  -o-transition-property: background-color, -o-transform;
  transition-property: background-color, transform;
  -webkit-transition-duration: 0.3s;
  -moz-transition-duration: 0.3s;
  -o-transition-duration: 0.3s;
  transition-duration: 0.3s;
}

.burger span::before, .burger span::after {
  position: absolute;
  content: "";
}

.burger span::before {
  top: -9px;
}

.burger span::after {
  top: 9px;
}

.burger.clicked span {
  background-color: transparent;
}

.burger.clicked span::before {
  -webkit-transform: translateY(9px) rotate(45deg);
  -moz-transform: translateY(9px) rotate(45deg);
  -ms-transform: translateY(9px) rotate(45deg);
  -o-transform: translateY(9px) rotate(45deg);
  transform: translateY(9px) rotate(45deg);
}

.burger.clicked span::after {
  -webkit-transform: translateY(-9px) rotate(-45deg);
  -moz-transform: translateY(-9px) rotate(-45deg);
  -ms-transform: translateY(-9px) rotate(-45deg);
  -o-transform: translateY(-9px) rotate(-45deg);
  transform: translateY(-9px) rotate(-45deg);
}

.burger:hover {
  cursor: pointer;
}

.form {
  padding: 1px 0px;
  display: flex;
  flex-direction: column;
  width: 100%;
  color: white;
  opacity: 0;
  position: relative;
}

.input-wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  margin-top: 60px;
}

.input-wrapper.first {
  margin-top: 0px;
}

.input-wrapper input {
  font-size: 16px;
  outline: none;
  border: 0;
  padding-top: 20px;
  padding-bottom: 15px;
  background: transparent;
  color: black;
  font-family: 'Avenir Regular', sans-serif;
  font-weight: 400;
}

.input-wrapper input:focus {
  border: none;
  color: black;
}

.input-wrapper label {
  position: absolute;
  font-family: 'Avenir Medium', sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 1em;
  bottom: 0px;
  left: 0;
  height: 100%;
  width: 100%;
  pointer-events: none;
  border-bottom: 1px solid #292B33;
}

.input-wrapper label::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: -1px;
  height: 100%;
  width: 100%;
  border-bottom: 2px solid;
  border-color: black;
  width: 0px;
  transition: width 0.5s ease, border-color .5s ease;
  align-self: center;
  justify-self: center;
}

.label-content {
  position: absolute;
  font-size: 18px;
  font-family: 'Ogg-Roman', 'Cormorant Garamond', serif;
  font-weight: 400;
  color: black;
  left: 0;
  bottom: 10px;
  opacity: 0.5;
  transform: translateY(-7px);
  transition: opacity 0.5s ease;
}

.focus + .label .label-content {
  opacity: 0;
}

.focus + .label:after {
  width: 100%;
}

.form-button {
  border: none;
  background: transparent;
  color: black;
  cursor: pointer;
  outline: none;
  font-size: 17px;
  overflow: hidden;
  position: relative;
  z-index: 2;
  width: 100%;
  height: 100%;
  font-family: 'Avenir Medium', sans-serif;
  font-weight: 500;
  line-height: 1em;
  padding: 0 0px;
  align-self: flex-end;
  position: absolute;
  bottom: -30px;
  width: fit-content;
}

.form-validate-text {
  color: #F44336;
  display: block;
  margin-top: 5px;
  font-size: 14px;
  position: absolute;
  transition: opacity .3s ease;
  opacity: 0;
  bottom: -27px;
}

